.banner_content,
.banner_content__img{
      width: 100%;
      height: 100%;
}

.banner_content{
      border-radius: 2rem;
      overflow: hidden;
}

.banner_content__img{
      background: var(--theme-200);
      position: relative;
}

.banner_content__img img{
      mix-blend-mode: multiply;
      border-radius: 2rem;
}

.banner_content__img:before{
      width: 100%;
      height: 100%;
      border-radius: 2rem;
      background: var(--theme-200);
      top: 0;
      left: 0;
      z-index: 2;
      opacity: .8; 
}

.banner_content__img.bg-grad::before{
      background-image: linear-gradient(rgba(255,187,185, .82), var(--theme-300));
}