/** aside */
.aside_content{
      width: 35.2rem;
      height: 100%;
      background: var(--base);
      border-radius: 2rem;
      border: solid 3.75rem var(--base);
      position: absolute;
      top: 0;
      left: 0;
      transform: translateX(0);
      transition: .5s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.aside_back{
      position: absolute;
      width: 100%;
      height: 100%;
      border-radius: 2rem;
      overflow: hidden;
      top: 0;
      left: 0;
}

.aside_front{
      width: 21rem;
      height: 100%;
      position: relative;
      z-index: 3;
      align-items: center;
      justify-content: center;
}

.user_wrapper{
      flex-direction: column;
      align-items: center;
      justify-content: center;
      gap: 1rem;
}

.user_avatar{
      width: 10rem;
      height: 10rem;
      position: relative;
      border-radius: 50%;
      box-shadow: 0 0 0 1rem var(--theme-200);
}

.user_avatar__img{
      width: 100%;
      height: 100%;
      border-radius: 50%;
      overflow: hidden;
}

.user_wrapper > h1{
      font-size: 3rem;
}

.user_wrapper > span{
      border-radius: 999px;
      padding: .3rem 1.5rem;
      background: var(--base);
      color: var(--theme-300);
}

.btn_chat{
      width: 4rem;
      height: 4rem;
      bottom: -2rem;
      left: -2rem;
      position: absolute;
      background: var(--base);
      border-radius: 50%;
      align-items: center;
      justify-content: center;
}

.btn_chat * {
      color: var(--theme-300);
      font-size: 2.5rem;
}