.online_content{
      width: 1.8rem;
      height: 1.8rem;
      background: var(--base);
      position: absolute;
      right: 1.35rem;
      bottom: 0;
      border-radius: 50%;
      padding: .4rem;
}

.online_dot{
      width: 100%;
      height: 100%;
      border-radius: 50%;
}

.online_dot.green{
      background: var(--green);
}

.online_dot.red{
      background: var(--red);
}