.pages_content{
      width: 49.5rem;
      height: 100%;
      position: absolute;
      top: 0;
      right: 0;
      box-shadow: 0 0 30px 5px rgba(0, 0, 0, .2);
      border-radius: 2rem;
      border: solid 1.5rem var(--base);
      overflow: hidden;
      background: var(--base);
}

.back_content{
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      border-radius: 2rem;
      overflow: hidden;
}

.front_content{
      position: relative;
      width: 100%;
      height: 100%;
      z-index: 3;
}

.home_content{
      position: relative;
      width: 100%;
      height: 100%;
}

.home_front{
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      align-items: center;
      justify-content: center;
      z-index: 3;
}

.show_youtube{
      width: 9.5rem;
      height: 9.5rem;
      padding: 1rem;
      background: rgba(255,249,241, .48);
      border-radius: 50%;
      transition: .3s;
      position: relative;
      z-index: 5;
}

.show_youtube.hide{
      visibility: hidden;
}

.show_youtube > span{
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      background: rgba(255,249,241, .48);
      border-radius: 50%;
      padding-left: .75rem;
      transition: .3s;
}

.show_youtube > span *{
      color: var(--theme-300);
      font-size: 3rem;
      transition: .3s;
}

.show_youtube:hover{
      padding: 0;
      box-shadow: 0 0 20px var(--theme-300);
}

.show_youtube:hover span{
      background: var(--theme-300);
}

.show_youtube:hover span *{
      color: var(--base);
}

.home_video{
      position: absolute;
      width: 43.4rem;
      height: 26rem;
      background: #121212;
      border-radius: 2rem;
      overflow: hidden;
      box-shadow: 0 0 30px rgba(0, 0, 0, .2);
      transform: translateY(-20%);
      transition: .4s cubic-bezier(0.165, 0.84, 0.44, 1);
      visibility: hidden;
      z-index: 6;
}

.home_video.show{
      transform: translateY(0);
      visibility: visible;
}

.home_video__iframe{
      width: 100%;
      height: 100%;
}

.home_video__iframe iframe{
      border: none;
      outline: none;
}

.close_video{
      position: absolute;
      width: 3.2rem;
      height: 3.2rem;
      background: var(--theme-300);
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      right: 1rem;
      top: calc(50% - 1.6rem);
      transform: translateX(200%);
      transition: .2s ease-out;
}

.close_video *{
      color: var(--base);
}

.home_video:hover .close_video{
      transform: translateX(0);
}

.toggle_menu{
      position: absolute;
      left: 1rem;
      bottom: 1rem;
      background: transparent;
}

.toggle_menu *{
      font-size: 3rem;
      color: var(--base);
}

.home_back{
      width: 100%;
      height: 100%;
      justify-content: center;
      align-items: flex-end;
      padding: 1rem;
      position: relative;
}

.home_description{
      width: 100%;
      height: auto;
      min-height: 15rem;
      position: relative;
      z-index: 2;
}

.home_description h2{
      font-size: 2.8rem;
}

.home_id{
      position: absolute;
      font-family: var(--ft-4);
      font-size: 12rem;
      line-height: 10rem;
      font-weight: 900;
      color: black;
      right: 0;
      bottom: 0;
      opacity: .2;
}

.nav_bar{
      width: 44rem;
      height: 4rem;
      position: absolute;
      bottom: 2rem;
      padding: .6rem;
      border-radius: 999px;
      align-items: center;
      justify-content: space-between;
      background: var(--base);
      transform: translateY(200%);
      transition: .5s cubic-bezier(0.165, 0.84, 0.44, 1);
      z-index: 10;
}

.nav_bar.active{
      transform: translateY(0);
}

.nav_bar__action{
      gap: 1rem;
      align-items: center;
}

.nav_bar__action > button{
      align-items: center;
      gap: 1rem;
      height: 2.8rem;
      padding: 0 1.4rem;
      background: var(--theme-300);
      border-radius: 999px;
      color: var(--base);
}

.nav_bar__action > button *{
      font-size: 2rem;
}

.nav_bar__action > button span{
      text-transform: capitalize;
      font-size: 1.6rem;
      padding-bottom: .4rem;
}

.close_menu{
      width: 2.8rem;
      height: 2.8rem;
      background: var(--theme-300);
      border-radius: 50%;
      align-items: center;
      justify-content: center;
      color: var(--base);
}

/**
 pages
*/

.wrapper_page{
      width: 43.5rem;
      height: 42.5rem;
      position: absolute;
      top: 1rem;
      border-radius: 1rem;
      z-index: 5;
      visibility: hidden;
}

.wrapper_page.active{
      visibility: visible;
}


.page_video,
.page_gallery{
      position: absolute;
      width: 100%;
      height: 100%;
      border-radius: 1rem;
      padding: 1rem;
      background: var(--theme-100);
      overflow-y: auto;
      transform: translateY(150%);
      transition: .5s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.page_video.active,
.page_gallery.active{
      transform: translateY(0);
}



.page_inner{
      width: 100%;
      height: 100%;
      overflow-y: auto;
}

.page_inner::-webkit-scrollbar{
      width: .8rem;
      border-radius: 999px;
      background: var(--theme-200);
}

.page_inner::-webkit-scrollbar-thumb{
      background: var(--base);
}
.close_page{
      width: 3rem;
      height: 4.6rem;
      background: linear-gradient(var(--theme-100), var(--theme-300));
      position: absolute;
      border-radius: 999px;
      align-items: center;
      justify-content: center;
      box-shadow: 0 0 20px rgba(0, 0, 0, .1);
      bottom: 2rem;
      left: calc(50% - 1.5rem);
      z-index: 3;
}

.close_page *{
      color: var(--base);
      font-size: 2rem;
}

.page_column{
      width: calc(100% - 1rem);
      height: auto;
      flex-direction: column;
      gap: 1.5rem;
      padding-bottom: 5rem;
}

.row_page{
      width: 100%;
      height: 21rem;
      border-radius: 1rem;
      overflow: hidden;
}

.row_page.photo{
      height: auto;
}